import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm, reset } from "redux-form";
import { getDataValidasiHutang } from "../../../actions/hutang_action.jsx";
import { getItem } from "../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import { Panel, PanelBody, PanelHeader } from "../../../components/panel/panel.jsx";
import { postDataNoTImeOut } from "../../../config/axios.jsx";
import ModuleReprintNotaHutang from "../ModuleReprintNotaHutang/index.jsx";
import HeadValidasi from "./HeadValidasi.jsx";

class FormValidasiHutang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      isLoadingBatal: false,
      //   DataValidasi: [],
      columns: [
        {
          dataField: "no_faktur_hutang",
          text: "No Faktur Hutang ",
        },
        // {
        //   dataField: "tgl_system",
        //   text: "Tanggal System ",
        // },
        // {
        //   dataField: "tgl_lunas",
        //   text: "Tgl Lunas ",
        // },
        // {
        //   dataField: "tgl_tempo",
        //   text: "Tgl Jth Tempo ",
        // },
        {
          dataField: "nama_customer",
          text: "Nama Customer ",
        },
        {
          dataField: "jumlah_hutang",
          text: "Jumlah Hutang ",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toLocaleString("kr-KO")}</span>;
          },
        },
        {
          dataField: "no_hp",
          text: "No Hp ",
        },
        {
          dataField: "alamat_customer",
          text: "Alamat ",
        },

        // {
        //   dataField: "lama_pinjam",
        //   text: "Lama Pinjam ",
        // },
        // {
        //   dataField: "bunga",
        //   text: "Bunga ",
        // },
        // {
        //   dataField: "total_hutang",
        //   text: "Total Hutang ",
        // },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    if (
      localStorage.getItem("validas_hutang") === null ||
      JSON.parse(localStorage.getItem("validas_hutang")).length === 0
    ) {
      ToastNotification("info", "No Faktur Hutang Belum Dipilih");
      this.setState({
        isLoading: false,
      });
    } else {
      let data_hutang = JSON.parse(
        localStorage.getItem("validas_hutang") || []
      );
      let hutang = {
        hutang: data_hutang,
      };
      this.setState({
        isLoading: true,
      });
      // console.log(hutang);
      postDataNoTImeOut("validasi/simpan/hutang", hutang)
        .then((res) => {
          this.setState({
            isLoading: false,
          });
          const module = getItem('module') || [];
          let cek = module.findIndex((list)=> list.key === "REPRINT_NOTA_HUTANG");
          if(cek >= 0){
            ModuleReprintNotaHutang(res.data.notahutang)
          }else{
         
let datahutang = [];
for (let index = 0; index < 2; index++) {
let feedback = '';
res.data.notahutang.forEach((row, index) => {
let jml_alamat = row.n_alamat_customer.length;
feedback += `========================================\n`
feedback += `              NOTA HUTANG               \n`
feedback += `========================================
Tanggal          : ${row.tgl_system}
Kode Sales       : ${row.kode_sales}
Nama Customer    : ${row.nama_customer}
No Hp            : ${row.no_hp}
Alamat Customer  : ${row.n_alamat_customer.slice(0,20)}
No Faktur Hutang : ${row.no_faktur_hutang.toLocaleString("kr-KO")}
`
if(jml_alamat>20){
feedback += `                   ${row.n_alamat_customer.slice(0,20)}
`;
}
if(jml_alamat>40){
feedback += `                   ${row.n_alamat_customer.slice(40,20)}
`;
}
feedback += `========================================\n`
feedback += `Keterangan\n`
feedback += `========================================\n`
row.detail_barang.forEach((detail, index) => {
feedback += `Nama Barang      : ${detail.nama_barang}\n`
feedback += `Kondisi          : ${detail.kondisi}\n`
feedback += `Kategori         : ${detail.kategori}\n`
feedback += `Jumlah           : ${detail.jumlah}\n`
feedback += `Berat            : ${detail.berat.toFixed(3)}\n`
feedback += `========================================\n`
})
feedback += `Berat            : ${row.berat.toFixed(3)}\n`
feedback += `Bunga            : ${row.bunga.toFixed(3)}\n`
feedback += `Jumlah Hutang    : ${row.jumlah_hutang.toLocaleString("kr-KO")}\n`
feedback += `========================================\n`
feedback += `                               TTD      \n`
feedback += `                                        \n`
feedback += `                                        \n`
feedback += `                                        \n`
feedback += `                         (             )\n`
feedback += `                                        \n`
feedback += `                                        \n`
feedback += `                                        \n`
}
)
  datahutang.push(feedback);
}          
  
    
          document.getElementById("myInput").value = datahutang.join('');
          setTimeout(() => {
            const element = document.createElement("a");
            const file = new Blob([document.getElementById("myInput").value], {
              type: "text/plain;charset=utf-8",
            });
            element.href = URL.createObjectURL(file);
            element.download = "autoprint_hutang.txt";
            document.body.appendChild(element);
            element.click();
          }, 300);
}
          ToastNotification("success", "Hutang Berhasil Di Validasi");
          localStorage.removeItem("validas_hutang");
          this.props.dispatch(reset("HeadValidasiHutang"));
          this.props.dispatch(getDataValidasiHutang([]));
        })
        .catch((err) => {
          this.setState({
            isLoading: false,
          });
          ToastNotification("info", "Gagal Mengambil Data Keserver");
        });
    }
    //   console.log();
  }
  clearHutang() {
    this.setState({
      isLoadingBatal: true,
    });
    setTimeout(() => {
      this.setState({
        isLoadingBatal: false,
      });
      localStorage.removeItem("validas_hutang");
      this.props.dispatch(reset("HeadValidasiHutang"));
      this.props.dispatch(getDataValidasiHutang([]));
    }, 1000);
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Kasir</Link>
          </li>
          <li className="breadcrumb-item active">Validasi Hutang</li>
        </ol>
        <h1 className="page-header">Validasi Hutang </h1>
        <Panel>
        <textarea
            style={{ display: "none" }}
            id="myInput"
            rows="100"
            cols="100"
          />
          <PanelHeader>Validasi Hutang</PanelHeader>
          {/* <button onClick={()=>this.Reprint()}> PRINT </button> */}
          <br />
          {/* Validasi Hutang */}
          <PanelBody>
                <HeadValidasi
                  columns={this.state.columns}
                  isLoading={this.state.isLoading}
                  isLoadingBatal={this.state.isLoadingBatal}
                  btnClear={()=>this.clearHutang()}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
          </PanelBody>
          <br />
          {/* End Tambah Validasi Hutang  */}
        </Panel>
      </div>
    );
  }
}

FormValidasiHutang = reduxForm({
  form: "FormValidasiHutang",
  enableReinitialize: true,
})(FormValidasiHutang);
export default connect()(FormValidasiHutang);
