import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
// import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import { getDataSales } from "../../../actions/datamaster_action";
import { getData, postData } from "../../../config/axios";
import { ToastNotification } from "../../../components/helpers/notification";
import Tabel from "../../../components/helpers/tabel";
import { getDataValidasiHutang } from "../../../actions/hutang_action";
import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  return {
    DataSales: state.datamaster.getDataSales,
    getValidasiHutang: state.hutang.getDataValidasiHutang,
  };
};
class HeadValidasi extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      DataValidasi: [],
      kode_sales: false,
    };
  }

  componentDidMount() {
    this.props.dispatch(getDataSales());
    this.getDetaValidasi()
  }
  getDataSales(e) {
    this.setState({
      kode_sales: e,
    });
    getData("sales/get/by-kode-sales/")
      .then((res) => {
        this.props.change("nama_sales", res.data.nama_sales);
      })
      .catch((err) => {
        ToastNotification("info", "Gagal Mengambil Data Keserver");
      });
  }
  getDetaValidasi() {
    this.setState({
      isLoading: true,
    });
    let hasil = {
      kode_sales: "all",
    };
    if (hasil.kode_sales === false) {
      this.setState({
        isLoading: false,
      });
      ToastNotification("info", "Kode Sales Harus Di pilih");
      this.setState({
        isLoading: false,
      });
      return false;
    }
    postData("validasi/get/hutang", hasil)
      .then((res) => {
        //   console.log(res.data[0].hutang);
        if (res.data[0].hutang.length === 0) {
          ToastNotification("info", "Belum Ada Data Validasi");
          this.setState({
            isLoading: false,
          });
        } else {
          ToastNotification("success", "Data Validasi Tersedia");
          this.setState({
            isLoading: false,
          });
          this.props.dispatch(getDataValidasiHutang(res.data[0].hutang));
        }
      })
      .catch((err) => {
        ToastNotification("info", "Terjadi Kesalahan Saat Request");
        this.setState({
          isLoading: false,
          DataValidasi: [],
        });
      });
  }

  render() {
    const selectRow = {
      mode: "checkbox",
      clickToSelect: true,
      selectColumnPosition: "left",
      onSelect: (row, isSelect, rowIndex, e) => {
        // console.log(isSelect);
        if (isSelect === true) {
          let hasil = {
            no_faktur_hutang: row.no_faktur_hutang,
          };
          let cek = JSON.parse(localStorage.getItem("validas_hutang")) || [];
          let hasil_cek = cek.findIndex(
            (hasil) => hasil.no_faktur_hutang === row.no_faktur_hutang
          );
          if (hasil_cek === -1) {
            cek.push(hasil);
            localStorage.setItem("validas_hutang", JSON.stringify(cek));
          }
        } else {
          // console.log();
          if (localStorage.getItem("validas_hutang") === null) {
          } else {
            let data = JSON.parse(localStorage.getItem("validas_hutang"));
            data.splice(row.no_faktur_hutang, 1);
            localStorage.setItem("validas_hutang", JSON.stringify(data));
          }
        }
      },
      onSelectAll: (isSelect, data, e) => {
        data.forEach((list, index) => {
          if (isSelect === true) {
            let hasil = {
              no_faktur_hutang: list.no_faktur_hutang,
            };
            let cek = JSON.parse(localStorage.getItem("validas_hutang")) || [];
            let hasil_cek = cek.findIndex(
              (hasil) => hasil.no_faktur_hutang === list.no_faktur_hutang
            );
            if (hasil_cek === -1) {
              cek.push(hasil);
              localStorage.setItem("validas_hutang", JSON.stringify(cek));
            }
          } else {
            if (localStorage.getItem("validas_hutang") === null) {
            } else {
              let data = JSON.parse(localStorage.getItem("validas_hutang"));
              data.splice(list.no_faktur_hutang, 1);
              localStorage.setItem("validas_hutang", JSON.stringify(data));
            }
          }
        });
      },
    };
    // const expandRow = {
    //   showExpandColumn: true,
    //   renderer: (row) => (
    //     <div className="row container">
    //       <div className="col-6">
    //         Nama Customer : {row.nama_customer}
    //         <br />
    //         No Hp : {row.no_hp}
    //         <br />
    //         Alamat Customer : {row.alamat_customer}
    //         <br />
    //         Berat Emas : {row.berat_emas}
    //         <br />
    //         Berat Bruto : {row.berat_bruto}
    //         <br />
    //         Total Bayar : {row.total_bayar}
    //         <br />
    //       </div>
    //       <div className="col-6">
    //         <table>
    //           <thead>
    //             <tr>
    //               <td>Nama Barang</td>
    //               <td>Kondisi</td>
    //               <td>Kategori</td>
    //               <td>Jumlah</td>
    //               <td>Berat</td>
    //             </tr>
    //           </thead>
    //           <tbody>
    //             <>
    //               {row.detail_barang.map((detail, index) => (
    //                 <tr>
    //                   <td> {detail.nama_barang} </td>
    //                   <td> {detail.kondisi} </td>
    //                   <td> {detail.kategori} </td>
    //                   <td> {detail.jumlah} </td>
    //                   <td> {detail.berat} </td>
    //                 </tr>
    //               ))}
    //             </>
    //           </tbody>
    //         </table>
    //       </div>
    //     </div>
    //   ),
    // };
    
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off"
      onKeyPress={(e) => {
        e.key === "Enter" && e.preventDefault();
      }}
      >
        <div className="row">
          {/* <div className="col-4">
            <Field
              name="kode_sales"
              onChange={(e) => this.getDataSales(e)}
              label="Pilih Kode Sales"
              placeholder="Masukan Kode Sales"
              options={this.props.DataSales.map((list) => {
                let data = {
                  value: list.kode_sales,
                  name: list.nama_sales,
                };
                return data;
              })}
              component={ReanderSelect}
            />
          </div>
          <div className="col-4">
            <Field
              name="nama_sales"
              component={ReanderField}
              type="text"
              label="Nama Sales"
              placeholder="Masukan Nama Sales"
              readOnly={true}
            />
          </div>
          <div className="col-4">
            <label>&nbsp;</label>
            <button
              onClick={() => this.getDetaValidasi()}
              className="btn btn-primary btn-block"
              disabled={this.state.isLoading}
              type="button"
            >
              {this.state.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Cari Data
                </>
              ) : (
                "Cari Data"
              )}
            </button>
          </div> */}
          <div className="col-12">
            <Tabel
              selectRow={selectRow}
              //   expandRow={expandRow}
              keyField="no_faktur_hutang"
              columns={this.props.columns}
              data={this.props.getValidasiHutang || []}
              empty={true}
            />
          </div>
          <div className="col-6">
            <button
              className="btn btn-primary btn-block"
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Validasi
                </>
              ) : (
                "Validasi"
              )}
            </button>
          </div>
          <div className="col-6">
            <button
              className="btn btn-warning btn-block"
              disabled={this.props.isLoadingBatal}
              onClick={this.props.btnClear}
              type="button"
            >
              {this.props.isLoadingBatal ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Batal
                </>
              ) : (
                "Batal"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading || this.props.isLoadingBatal || this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadValidasi = reduxForm({
  form: "HeadValidasiHutang",
  enableReinitialize: true,
})(HeadValidasi);
export default connect(maptostate)(HeadValidasi);
